'use client'
import { useState } from "react"

export default function Home() {
    const [count, setCount] = useState(100);
    return (
        <div className="page.color">
        <div>
            <h1>state</h1> 
            <div>{count}</div>
            <button onClick={() => {setCount(count + 1)}}>+</button>
        </div>
        </div>
    )
}